.hero {
	background-color    : #E5E3E6;
	background-image    : url("../images/home/hero/hero-mobile@2x.webp");
	background-position : center right;
	background-repeat   : no-repeat;
	background-size     : 360px 621px;
	display             : flex;
	flex-direction      : column;
	height              : 100vh;
	justify-content     : end;
	padding             : 359px 0 64px 0;

	.no-webp & {
		background-image : url("../images/home/hero/hero-mobile@2x.jpg");
	}

	@media screen and (min-width : $break-table) {
		background-image : url("../images/home/hero/hero-tablet@2x.webp");
		background-size  : contain;
		justify-content  : center;
		padding          : 50px 0;

		.no-webp & {
			background-image : url("../images/home/hero/hero-tablet@2x.jpg");
		}
	}

	@media screen and (min-width : $break-desktop) {
		background-image : url("../images/home/hero/hero@2x.webp");
		background-size  : 1440px 797px;
		padding          : 50px 0;

		.no-webp & {
			background-image : url("../images/home/hero/hero@2x.jpg");
		}
	}
}

.hero__wrapper {
	text-align : center;

	@media screen and (min-width : $break-table) {
		text-align : start;
	}

	.b-btn--primary {
		min-width : 192px;

		@media screen and (min-width : $break-desktop) {
			min-width : 224px;
		}
	}
}

.hero__title {
	color         : $primary;
	font-size     : 80px;
	font-weight   : 500;
	line-height   : 96px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 148px;
		line-height   : 160px;
		margin-bottom : 12px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 218px;
		line-height   : 237px;
		margin-bottom : 24px;
	}
}
