.target__wrapper {
	display               : grid;
	grid-gap              : 26px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap : 34px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 45px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.target__main {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
	grid-column    : 4 span;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 5 span;
	}
}

.target__text {
	color         : $dark;
	font-size     : 18px;
	font-weight   : 500;
	line-height   : 24px;
	margin-bottom : 33px;

	&:before {
		background-image  : url("../images/icon/quotes.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : block;
		height            : 64px;
		margin-left       : -12px;
		width             : 64px;

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 12px;
		}
	}

	@media screen and (min-width : $break-table) {
		max-width : 470px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 28px;
	}
}

.target__arrow {
	align-items : center;
	display     : flex;
	margin-top  : auto;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	img {
		transform : rotate(90deg);

		@media screen and (min-width : $break-desktop) {
			transform : rotate(0deg);
		}

	}

	span {
		color       : $dark;
		font-size   : 14px;
		font-style  : normal;
		font-weight : 400;
		line-height : 20px;
		margin-left : 24px;
		max-width   : 230px;
	}


}

.target__video {
	grid-column : 4 span;
	position    : relative;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 7 span;
	}
}

.video {


	&:hover {
		.video__btn {
			svg {
				circle {
					fill       : $primary;
					transition : fill .3s ease;
				}
			}
		}
	}
}

.video__poster {
	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.video__btn {
	left      : 50%;
	position  : absolute;
	top       : 50%;
	transform : translate(-50%, -50%);
	z-index   : 1;


	svg {
		circle {
			fill       : #333;
			transition : fill .3s ease;
		}
	}
}
