.footer__grid {
	border-bottom         : 1px solid #E6E6E6;
	border-top            : 1px solid #E6E6E6;
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.footer__item {
	display         : flex;
	flex-direction  : row-reverse;
	grid-column     : 4 span;
	justify-content : space-between;
	padding         : 32px 32px;

	@media screen and (min-width : $break-table) {
		display : block;
		padding : 24px 24px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 24px 24px 48px 0;
	}

	&:not(:first-child) {
		border-top : 1px solid #E6E6E6;

		@media screen and (min-width : $break-table) {
			border-left : 1px solid #E6E6E6;
			border-top  : none;
			padding     : 24px 24px;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 24px 24px 48px 60px;
		}
	}
}

.footer__icon {
	display         : flex;
	justify-content : flex-end;
	margin-bottom   : 36px;
}

.footer__name {
	color         : $dark;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 16px;
	opacity       : 0.7;

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.footer__link {
	color       : $dark;
	font-size   : 16px;
	font-weight : 500;
	line-height : 22px;

	@media screen and (min-width : $break-table) {
		font-size   : 18px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 28px;
	}
}

.footer__bottom {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	gap            : 16px;
	padding        : 32px 0;

	@media screen and (min-width : $break-table) {
		flex-direction  : row;
		gap             : 0;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}

	a {
		color       : $dark;
		font-size   : 12px;
		font-style  : normal;
		font-weight : 500;
		line-height : 18px;
		opacity     : 0.7;
	}
}


.s-block__link {
	& + & {
		margin-left : 8px;
	}

	svg {
		circle {
			fill       : #333;
			transition : fill .3s ease;
		}
	}

	&:hover {
		svg {
			circle {
				fill       : $primary;
				transition : fill .3s ease;
			}
		}
	}
}
