
.overlay {
	background : rgba(0, 0, 0, 0.7);
	bottom     : 0;
	left       : 0;
	opacity    : 0;
	position   : fixed;
	right      : 0;
	//z-index: -1;
	top        : 0;
	transition : opacity 0.3s ease, visibility .3s ease;
	visibility : hidden;
	z-index    : 101;

	&.active {
		opacity    : 1;
		visibility : visible;
	}
}

.popup {
	display    : none;
	left       : 50%;
	max-width  : 384px;
	padding    : 0 15px;
	//z-index: -3;
	position   : fixed;
	top        : 50%;
	transform  : translate(-50%, -50%);
	transition : opacity 0.5s ease;
	//opacity: 0;
	width      : 100%;
	z-index    : 103;

	@media screen and (min-width : $break-table) {
		max-width : 510px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&.active {
		display    : block;
		//opacity: 1;
		transition : opacity 0.5s ease;
		//transform: translate(-50%, -45%);
		z-index    : 103;
	}

	&__wrap {
		align-items      : center;
		background-color : #FFF;
		display          : flex;
		min-height       : 220px;
		overflow         : hidden;
		position         : relative;
	}

	&__close {
		background-color : transparent;
		border           : none;
		height           : 32px;
		padding          : 0;
		position         : absolute;
		right            : 26px;
		top              : 12px;
		width            : 32px;
		z-index          : 999;

		svg {
			height : 32px;
			width  : 32px;
		}

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}


