.colleagues__title {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 42px;
	}
}

.colleagues__grid {
	border                : 1px solid #E8EBED;
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.colleagues-item {
	display        : flex;
	flex-direction : column;
	grid-column    : 4 span;
	padding        : 32px 24px 32px 24px;

	&:not(:last-child) {
		border-bottom : 1px solid #E8EBED;
	}

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		padding     : 48px 48px;

		&:not(:last-child) {
			border-bottom : none;
		}

		&:not(:first-child) {
			border-left : 1px solid #E8EBED;
		}
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 4 span;
		padding     : 36px 48px 50px 50px;

		&:not(:first-child) {
			border-left : none;
		}
	}
}

.colleagues-item__count {
	align-items : flex-end;
	display     : flex;

	div {
		&:first-child {
			color         : $primary;
			font-size     : 64px;
			font-weight   : 500;
			line-height   : 80px;
			margin-bottom : 24px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				font-size     : 80px;
				font-weight   : 500;
				line-height   : 117.939px;
				margin-bottom : 0;
			}
		}

		&:last-child {
			color         : $dark;
			font-size     : 14px;
			font-weight   : 500;
			line-height   : 20px;
			margin-bottom : 37px;
			margin-left   : 19px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 28px;
				margin-left   : 19px;
			}
		}
	}
}

.colleagues__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 500;
	line-height : 20px;
	margin-top  : auto;


	@media screen and (min-width : $break-table) {
		max-width : 270px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.colleagues__img {
	grid-column : 4 span;
	grid-row    : 1/1;
	@media screen and (min-width : $break-table) {
		grid-column : 12 span;

	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 4 span;
		grid-row    : auto;
	}

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}
