.header {
	left        : 50%;
	padding-top : 24px;
	position    : absolute;
	top         : 0;
	transform   : translateX(-50%);
	width       : 100%;
	z-index     : 10;
}

.header__top {
	align-items     : center;
	display         : flex;
	justify-content : space-between;

	@media screen and (min-width : $break-desktop) {
		border-bottom  : 1px solid rgba(51, 51, 51, 0.15);
		padding-bottom : 14px;
	}
}

.header__logo {
	height   : 68px;
	position : relative;
	width    : 116px;
	z-index  : 10;
}

.header__panel {
	display : none;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		align-items : center;
		display     : flex;
	}

	.b-btn--outline-primary {
		min-width : 224px;
	}
}

.header-contacts {
	align-items  : center;
	display      : flex;
	margin-right : 48px;
}

.header-contacts__item {
	& + & {
		margin-left : 24px;
	}
}

.header-contacts__link {
	align-items : center;
	display     : flex;

	span {
		color       : $dark;
		font-size   : 14px;
		font-weight : 500;
		line-height : 20px;
		margin-left : 16px;
	}
}

.header__wrap {
	display        : flex;
	flex-direction : column;
	height         : 100%;
	max-height     : 700px;
}

.header__bottom {
	background-color  : #ECEAED;
	background-image  : url("../images/home/header/menu-bg-mob.webp");
	background-repeat : no-repeat;
	background-size   : cover;
	display           : flex;
	flex-direction    : column;
	height            : 100dvh;
	justify-content   : center;
	opacity           : 0;
	overflow-x        : hidden;
	overflow-y        : auto;
	padding-top       : 104px;
	position          : absolute;
	right             : -100%;
	top               : 0;
	transition        : visibility .3s ease, opacity .3s ease, right .3s ease;
	visibility        : hidden;
	width             : 100%;
	z-index           : 9;

	.no-webp & {
		background-image : url("../images/home/header/menu-bg-mob.jpg");
	}

	@media screen and (min-width : $break-table) {
		background-image : url("../images/home/header/menu-bg-tablet.webp");

		.no-webp & {
			background-image : url("../images/home/header/menu-bg-tablet.jpg");
		}
	}

	@media screen and (min-width : $break-desktop) {
		background-color : transparent;
		background-image : unset;
		height           : auto;
		opacity          : 1;
		overflow         : visible;
		padding-top      : 24px;
		position         : unset;
		right            : unset;
		visibility       : visible;
	}

	&.active {
		opacity    : 1;
		right      : 0;
		visibility : visible;
	}
}

.nav {
	margin-bottom : 40px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 64px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
	}
}

.nav__list {
	align-items    : center;
	display        : flex;
	flex-direction : column;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		flex-direction  : row;
		justify-content : space-between;
	}
}

.nav__item {
	&:not(:last-child) {
		margin-bottom : 16px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 32px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 0;
		}
	}
}

.nav__link {
	color       : $dark;
	font-size   : 20px;
	font-weight : 500;
	line-height : 26px;
	position    : relative;
	transition  : color .3s ease;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		line-height : 42px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 20px;
	}

	&:hover {
		color : $primary;

		&:after {
			width : 100%;
		}
	}

	&:after {
		background-color : $primary;
		bottom           : -6px;
		content          : "";
		height           : 1px;
		left             : 0;
		position         : absolute;
		transition       : width .3s ease;
		width            : 0;
		z-index          : 1;
	}
}

.header__burger {
	align-items      : center;
	background-color : $primary;
	border-radius    : 100%;
	display          : flex;
	height           : 36px;
	justify-content  : center;
	position         : relative;
	width            : 36px;
	z-index          : 10;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.contacts {
	align-items    : center;
	display        : flex;
	flex-direction : column;

	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.contacts__name {
	color         : $dark;
	font-size     : 12px;
	font-style    : normal;
	font-weight   : 400;
	line-height   : 18px;
	margin-bottom : 16px;
	opacity       : 0.7;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 22px;
		margin-bottom : 19px;
	}
}

.contacts__link {
	color       : $dark;
	font-size   : 16px;
	font-style  : normal;
	font-weight : 500;
	line-height : 22px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 28px;
	}
}

.contacts__item {
	&:not(:last-child) {
		margin-bottom : 24px;
	}
}

.burger {
	background-color : transparent;
	border           : none;
	color            : $white;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 12px;
	padding          : 0;
	position         : relative;
	width            : 16px;

	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 1px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
		z-index          : 3;
	}

	&::before {
		top : 1px;
	}

	&::after {
		top : calc(100% - 2.5px);
	}

	&__line {
		background-color : currentColor;
		border-radius    : 10px;
		height           : 1px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 100%;
	}

	&--active {
		&::before {
			top        : 50%;
			transform  : rotate(45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		&::after {
			top        : 50%;
			transform  : rotate(-45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		.burger__line {
			transform  : scale(0);
			transition : transform 0.3s ease-in-out;
		}
	}
}
