* {
	-webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}

html {
	box-sizing : border-box;
	height     : 100%;
}

*,
*::before,
*::after {
	box-sizing : inherit;
}

.page-wrapper {
	display        : flex;
	flex-direction : column;
	min-height     : 100%;
	overflow       : hidden;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.main {
	flex : 1 0 auto;
}

.main--offset {
	border        : 1px solid #CCD9E5;
	border-radius : 8px;
}

.container {
	margin    : 0 auto;
	max-width : 1198px;
	padding   : 0 14px;
	width     : 100%;

	@media screen and (min-width : $break-table) {
		max-width : 1218px;
		padding   : 0 24px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.section-offset {
	padding : 32px 0;

	@media screen and (min-width : $break-table) {
		padding : 40px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 60px 0;
	}
}

.section-title {
	color       : $dark;
	font-size   : 28px;
	font-weight : 500;
	line-height : 36px;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		line-height : 42px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		line-height : 56px;
	}
}

.scroll {
	height   : 100vh;
	overflow : hidden;
	position : relative;
	width    : 100vw;
}
