.burger {
  background-color: transparent;
  border: none;
  color: #FFF;
  cursor: pointer;
  flex-shrink: 0;
  height: 12px;
  padding: 0;
  position: relative;
  width: 16px;
}
.burger::before, .burger::after {
  background-color: currentColor;
  content: "";
  height: 1px;
  left: 0;
  position: absolute;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  width: 100%;
  z-index: 3;
}
.burger::before {
  top: 1px;
}
.burger::after {
  top: calc(100% - 2.5px);
}
.burger__line {
  background-color: currentColor;
  border-radius: 10px;
  height: 1px;
  left: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
  width: 100%;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

