.practice.section-offset {
	padding-bottom : 64px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 80px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 120px;
	}
}

.practice__title {
	margin-bottom : 32px;
	max-width     : 332px;

	@media screen and (min-width : $break-table) {
		max-width : 450px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 42px;
		max-width     : 516px;
	}
}

.practice__wrapper {
	display               : grid;
	grid-gap              : 32px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 50px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.practice__left {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}
}

.practice__subtitle {
	color         : $dark;
	font-size     : 20px;
	font-weight   : 500;
	line-height   : 26px;
	margin-bottom : 16px;
	max-width     : 311px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 28px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}

.practice__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		max-width : 542px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.practice-card {
	align-items    : flex-start;
	background     : rgba(228, 226, 229, 0.32);
	display        : flex;
	flex-direction : column;
	grid-column    : 4 span;
	padding        : 32px 24px;

	@media screen and (min-width : $break-table) {
		flex-direction : row;
		padding        : 48px 28px 48px 48px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}
}

.practice-card__img {
	flex-shrink   : 0;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}
}

.practice-card__wrap {

	@media screen and (min-width : $break-table) {
		margin-left : 26px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.practice-card__name {
	color         : $dark;
	font-size     : 20px;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 7px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 28px;
		margin-bottom : 12px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.practice-card__position {
	color         : $dark;
	font-size     : 14px;
	font-style    : normal;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 16px;
	opacity       : 0.7;

	@media screen and (min-width : $break-table) {
		margin-bottom : 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.practice-card__about {
	color         : $dark;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
		max-width     : 300px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
