.about__title {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 42px;
	}
}

.about__grid {
	border                : 1px solid #E8EBED;
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.about-item {
	grid-column : 4 span;
	padding     : 24px 24px 32px 24px;

	&:not(:last-child) {
		border-bottom : 1px solid #E8EBED;
	}

	@media screen and (min-width : $break-table) {
		align-items : center;
		display     : flex;
		grid-column : 12 span;
		padding     : 20px 20px;
	}

	@media screen and (min-width : $break-desktop) {
		display     : block;
		grid-column : 4 span;
		padding     : 20px 20px;

		&:not(:first-child) {
			border-left : 1px solid #E8EBED;
		}
	}

}

.about-item__img {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		flex-shrink   : 0;
		margin-bottom : 0;
		padding-left  : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 18px;
		padding-left  : 0;
	}
}

.about-item__wrap {
	padding-left : 11px;

	@media screen and (min-width : $break-table) {
		margin-left   : 67px;
		padding-left  : 0;
		padding-right : 66px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left   : 11px;
		max-width     : 248px;
		padding-right : 0;
	}
}

.about-item__title {
	color         : $dark;
	font-size     : 20px;
	font-weight   : 500;
	line-height   : 26px;
	margin-bottom : 16px;
	max-width     : 137px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 28px;
		max-width   : 173px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.about-item__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;
}
