.choice__wrapper {
	background-color    : #E4E2E5;
	background-image    : url("../images/home/choice/choice-bg@2x.webp");
	background-position : center center;
	background-repeat   : no-repeat;
	background-size     : cover;
	padding             : 64px 0;

	.no-webp & {
		background-image : url("../images/home/choice/choice-bg@2x.jpg");
	}

	@media screen and (min-width : $break-table) {
		padding : 80px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0;
	}
}


.choice__title {
	margin-bottom : 64px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 96px;
	}
}

.choice__list {

}

.choice__item {

	&:not(:last-child) {
		margin-bottom : 64px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 100px;
		}

	}

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}


}


.choice__name {
	color         : $dark;
	font-size     : 20px;
	font-weight   : 500;
	line-height   : 26px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 28px;
		margin-bottom : 0;
		max-width     : 317px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.choice__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;


	@media screen and (min-width : $break-table) {
		max-width : 340px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 440px;
	}
}
