.services__title {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 42px;
	}
}

.tabs__wrap {
	margin     : 0 -15px;
	overflow-x : auto;

	@media screen and (min-width : $break-table) {

	}
}

.tabs__head {
	align-items   : center;
	display       : flex;
	margin-bottom : 35px;
	padding       : 0 15px 25px 15px;
	width         : 732px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.tabs__button {
	background-color : transparent;
	border           : none;
	color            : $dark;
	display          : flex;
	font-size        : 16px;
	font-weight      : 400;
	justify-content  : flex-start;
	line-height      : 22px;
	min-width        : 234px;
	opacity          : 0.6;
	padding          : 0;
	position         : relative;
	transition       : opacity .3s ease;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 18px;
		line-height : 20px;
		min-width   : 380px;
	}

	&:hover {
		opacity : 1;

		&:before {
			width : 100%;
		}
	}

	&.active {
		opacity : 1;

		&:before {
			width : 100%;
		}
	}

	&:before {
		background-color : $dark;
		bottom           : -26px;
		content          : "";
		height           : 1px;
		left             : 0;
		position         : absolute;
		transition       : width .3s ease;
		width            : 0;
		z-index          : 2;
	}

	&:after {
		background-color : #E6E6E6;
		bottom           : -26px;
		content          : "";
		height           : 1px;
		left             : 0;
		position         : absolute;
		width            : 100%;
		z-index          : 1;
	}
}

.tabs__content {
	display : none;

	&.active {
		display : block;
	}
}

.services-item {
	background-color      : rgba(228, 226, 229, 0.32);
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	padding               : 32px 24px 124px 24px;
	position              : relative;

	@media screen and (min-width : $break-table) {
		background-color      : transparent;
		grid-gap              : 20px;
		grid-template-columns : repeat(12, 1fr);
		padding               : 0;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 30px;
	}
}

.services-item__left {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
	grid-column    : 4 span;
	margin-bottom  : 24px;

	@media screen and (min-width : $break-table) {
		align-items      : flex-end;
		background-color : rgba(228, 226, 229, 0.32);
		flex-direction   : row;
		grid-column      : 12 span;
		justify-content  : space-between;
		margin-bottom    : 0;
		padding          : 48px 48px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items    : flex-start;
		flex-direction : column;
		grid-column    : 1/5;
	}

	.b-btn--primary {
		bottom     : 24px;
		left       : 24px;
		margin-top : 24px;
		position   : absolute;

		@media screen and (min-width : $break-table) {
			bottom     : unset;
			left       : unset;
			margin-top : 0;
			position   : unset;
		}

		@media screen and (min-width : $break-desktop) {
			margin-top : auto;
		}
	}
}

.services-item__title {
	color         : $dark;
	font-size     : 24px;
	font-weight   : 500;
	line-height   : 28px;
	margin-bottom : 16px;
	max-width     : 120px;

	@media screen and (min-width : $break-table) {
		max-width : none;
	}
}

.services-item__subtitle {
	color       : $dark;
	font-size   : 14px;
	font-weight : 500;
	line-height : 20px;
	max-width   : 164px;
}

.services-item__right {
	background-color : rgba(228, 226, 229, 0.32);
	grid-column      : 4 span;

	@media screen and (min-width : $break-table) {
		align-items : start;
		display     : flex;
		grid-column : 12 span;
		padding     : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 5/13;
		padding     : 48px 48px;
	}
}

.services-item__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.services-item__img {
	flex-shrink : 0;
	height      : 140px;
	position    : absolute;
	right       : -11px;
	text-align  : center;
	top         : -21px;
	width       : 140px;

	@media screen and (min-width : $break-table) {
		height   : auto;
		margin   : -40px -60px -40px 20px;
		position : unset;
		right    : unset;
		top      : unset;
		width    : auto;
	}

	@media screen and (min-width : $break-desktop) {
		margin : -40px -90px -40px 20px;
	}
}
